.effective {
	position: relative;
	padding-top: 100px;
	background-image: url('../img/bg-effective.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media (max-width: 575px) {
		padding-top: 50px;
	}


	&__desc {
		background: rgb(17, 99, 194);
		background: linear-gradient(90deg, rgba(17, 99, 194, 1) 53%, rgba(143, 39, 251, 1) 94%);
		font-weight: 700;
		line-height: calc(30 / 16);
		text-transform: uppercase;
		letter-spacing: 0.058em;
		color: #fff;
		border-radius: 10px;
		text-align: center;
		padding: 35px 0;
		margin-top: 35px;

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

	&__content {
		margin-top: 25px;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 25px 55px;
		counter-reset: effective-num;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 575px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__item {
		background-color: #fff;
		box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.11);
		padding: 15px 25px;
		border-radius: 10px;

		&:hover {
			.effective__num {
				color: #0967be;
			}
		}
	}

	&__top {
		@include jcsb;
	}

	&__subtitle {
		font-size: 18px;
		line-height: calc(21 / 18);
		font-weight: 700;
		color: #0967be;
	}

	&__num {
		font-size: 55px;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: 0.058em;
		color: #adadad;
		text-decoration: underline;
		transition: all 0.3s ease;

		&::before {
			content: '0'counter(effective-num);
			counter-increment: effective-num;
		}

	}

	&__text {
		margin-top: 20px;
		line-height: calc(21 / 16);
		color: #7a7a7a;
	}
}