.team {
	margin-top: 150px;
	padding: 55px 0 40px;
	background-image: url('../img/bg-team.jpg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__content {
		display: flex;

		@media (max-width: 1160px) {
			flex-direction: column;
			gap: 60px;
		}
	}

	&__left {
		@media (min-width: 1161px) {
			flex: 0 0 40%;
		}
	}

	&__subtitle {
		color: #fff;
		line-height: calc(26 / 16);
		margin-top: 70px;
		max-width: 315px;

		@media (max-width: 1160px) {
			max-width: 100%;
			margin-top: 30px;
		}
	}

	&__text {
		color: #fff;
		line-height: calc(26 / 16);
	}

	&__btn {
		margin-top: 50px;
		padding: 30px 60px 30px 30px;
		font-size: 16px;
		text-transform: none;

		@media (max-width: 1160px) {
			margin-top: 0;
		}

		@media (max-width: 575px) {
			padding: 20px;
		}

		&.btn--arrow::after {
			right: 12px;
			width: 25px;
		}
	}

	&__right {
		@media (min-width: 1161px) {
			flex: 0 0 60%;
		}
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 30px;
		margin-bottom: 37px;

		@media (max-width: 1160px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__item {
		text-align: center;
	}

	&__img {
		margin: auto;
		width: 128px;
		height: 128px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__name {
		margin-top: 15px;
		font-size: 18px;
		line-height: calc(24 / 18);
		font-weight: 700;
		color: #fff;
	}

	&__desc {
		color: #fff;
		margin-top: 15px;
	}

	&__num {
		margin: auto;
		width: 128px;
		height: 128px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: rgba(0, 0, 0, 0.4);
		font-size: 48px;
		font-weight: 900;
		color: #fff;
	}
}