.feedback {
	position: relative;
	background-color: #F4F4F3;
	padding: 55px 0;

	&__subtitle {
		position: relative;
		margin-top: 15px;
		font-size: 40px;
		font-weight: 900;
		color: #727279;

		@media (max-width: 991px) {
			font-size: 28px;
		}

		@media (max-width: 575px) {
			font-size: 18px;
		}
	}

	&__form {
		position: relative;
		margin-top: 30px;

		textarea {
			width: 100%;
			height: 137px;
			border-radius: 10px;
			padding: 20px;
			color: #8b8b8b;
			outline: none;
		}
	}

	&__group {
		display: flex;
		gap: 30px;
		margin-top: 30px;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
			margin-top: 15px;
		}

		input {
			width: 100%;
			height: 80px;
			border-radius: 10px;
			padding: 0 20px;
			color: #8b8b8b;
			outline: none;
		}
	}

	&__item {
		font-size: 18px;
		line-height: calc(29 / 18);
		font-weight: 400;
		color: #727279;

		@media (min-width: 992px) {
			max-width: 385px;
			margin-left: 150px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__mail {
		color: $color-primary;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
			color: $color-primary;
		}
	}

	&__tel {
		color: $color-primary;
		font-weight: 700;

		&:hover {
			color: inherit;
		}
	}
}