.influence {
	position: relative;

	&--1 {
		margin-top: 130px;

		@media (max-width: 575px) {
			margin-top: 50px;
		}
	}

	&--2 {
		margin-top: 100px;
		padding: 40px 0;
		background-color: #F5F5F5;

		@media (max-width: 575px) {
			margin-top: 50px;
		}
	}

	&__content {
		&--2 {
			padding: 40px 0;
			background-color: #fff;
			border-radius: 10px;
		}
	}

	&__title {
		padding: 0 40px;
		font-size: 40px;
		font-weight: 900;
		color: #727279;

		@media (max-width: 991px) {
			font-size: 28px;
		}

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
			font-size: 20px;
		}
	}

	&__subtitle {
		position: relative;
		padding: 0 40px;
		margin-top: 15px;
		font-size: 16px;
		line-height: calc(24 / 16);
		font-weight: 700;
		color: #8d8d8d;

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
			font-size: 14px;
		}
	}

	&__list {
		counter-reset: num;
		background-color: #F5F9FC;
		padding: 20px 20px 20px 60px;
		margin-top: 30px;
		border-left: 11px solid #0967BE;

		@media (max-width: 991px) {
			padding-left: 30px;
		}

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	&__item {
		position: relative;
		font-weight: 300;
		color: #8d8d8d;
		padding-left: 30px;

		@media (max-width: 575px) {
			font-size: 14px;
			padding-left: 20px;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			position: absolute;
			left: 0;
		}

		&--num {
			&::before {
				content: counter(num);
				counter-increment: num;
				color: #0967BE;
				font-weight: 700;
			}
		}

		&--check {
			&::before {
				background-image: url('../img/svg/check-2.svg');
				background-size: cover;
				top: 3px;
				width: 16px;
				height: 16px;
			}
		}

		&--disc {
			&::before {
				background-image: url('../img/svg/circle.svg');
				background-size: 8px;
				top: 3px;
				width: 16px;
				height: 16px;
			}
		}
	}

	&__item+&__item {
		margin-top: 15px;
	}

	&__link {
		color: $color-primary;
		text-decoration: underline;

		&:hover {
			color: $color-primary;
			text-decoration: none;
		}
	}

	&__text {
		padding: 0 40px;
		margin-top: 30px;
		line-height: calc(24 / 16);
		color: #8d8d8d;
		font-weight: 300;

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
			font-size: 14px;
		}

		span {
			font-weight: 700;
		}
	}
}