.projects {
	margin-top: 125px;

	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 15px 35px;
		margin-top: 60px;

		@media (max-width: 575px) {
			grid-template-columns: 1fr;
			margin-top: 30px;
		}
	}

	&__item {
		border-radius: 10px;
		background-color: #ededed;
		padding: 30px 25px;
		display: flex;
		align-items: center;
		gap: 25px;
		transition: all 0.3s ease;

		@media (max-width: 991px) {
			flex-direction: column;
			text-align: center;
			padding-left: 15px;
			padding-right: 15px;
		}

		&:hover {
			background-color: #ddd;
		}
	}

	&__icon {
		width: 100%;
		max-width: 60px;
		height: 60px;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__subtitle {
		font-size: 18px;
		line-height: calc(21 / 18);
		font-weight: 700;
		color: #0967be;
	}

	&__desc {
		margin-top: 20px;
		line-height: calc(21 / 16);
		color: #7a7a7a;
	}
}