.tabs {
	position: relative;
	z-index: 1;
	margin-top: 40px;

	&__list {
		list-style-type: none;
		padding: 0;
		margin: 0;
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
		}

		@media (max-width: 575px) {
			width: 100%;
		}
	}

	&__item {
		border-radius: 10px 10px 0 0;
		background-color: #ddd;
		padding: 15px 62px;
		border: none;
		color: #7a7a7a;
		cursor: pointer;
		font-weight: 700;
		font-size: 18px;
		text-align: center;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

		@media (max-width: 991px) {
			width: 100%;
			border-radius: 0;
		}

		@media (max-width: 575px) {
			padding-left: 0;
			padding-right: 0;
		}

		&:first-child {
			@media (max-width: 991px) {
				border-radius: 10px 10px 0 0;
			}
		}

		&:hover {
			color: #4f4f4f;
		}

		&.active {
			background-color: #F0F0F0;
			color: #4f4f4f;
			transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
			pointer-events: none;
		}
	}

	&__content {
		display: none;
		background-color: #F0F0F0;
		padding: 30px;
		border-radius: 0 10px 10px 10px;

		@media (max-width: 575px) {
			border-radius: 0 0 10px 10px;
			padding: 15px;
		}

		&.active {
			display: block;
		}
	}

	.swiper-nav {
		display: flex;
		align-items: center;
		margin-top: 32px;
	}

	.swiper-button-prev {
		background-image: url("../img/svg/arrow-left-slide.svg");
	}

	.swiper-button-next {
		background-image: url("../img/svg/arrow-right-slide.svg");
		margin-left: 20px;
	}

	.swiper-button-next::after,
	.swiper-button-prev::after {
		content: "";
	}

	.swiper-button-prev,
	.swiper-button-next {
		position: inherit;
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		transition: all 0.3s ease;
		width: 100%;
		max-width: 50px;
		height: 50px;
		z-index: 11;
		margin-top: 0;
		filter: invert(50%);

		@media (max-width: 575px) {
			max-width: 40px;
			height: 40px;
		}
	}

	.swiper-pagination-bullet {
		width: 100%;
		height: 11px;
		border-radius: 4px;
		background-color: transparent;

		@media (max-width: 575px) {
			height: 8px;
		}
	}

	.swiper-pagination-bullet-active {
		background: #fff;
	}

	.swiper-horizontal>.swiper-pagination-bullets,
	.swiper-pagination-bullets.swiper-pagination-horizontal {
		position: inherit;
		background-color: #c5c5c5;
		border-radius: 8px;
		height: 19px;
		margin: 0 20px;
		padding: 0 5px;
		display: flex;
		align-items: center;

		@media (max-width: 575px) {
			height: 14px;
		}
	}
}