.reviews {
	position: relative;
	margin-top: 80px;

	&__title {
		text-align: center;

		span {
			display: inline-block;
		}
	}

	&__swiper {
		margin-top: 40px;
	}

	&__slide {
		padding: 0 90px;

		@media (max-width: 575px) {
			padding: 0 0;
		}
	}

	&__top {
		@include jcsb;

		@media (max-width: 575px) {
			flex-direction: column;
			align-items: flex-start;
			gap: 20px;
		}
	}

	&__subtitle {
		font-size: 24px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.058em;
		color: #0967be;
		margin-bottom: 5px;
	}

	&__link {
		text-decoration: underline;
		letter-spacing: 0.058em;
		color: #0967be;

		&:hover {
			color: #0967be;
			text-decoration: none;
		}
	}

	&__img {
		width: 50px;
		height: 50px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__excerpt {
		margin-top: 20px;
		font-size: 12px;
		line-height: calc(21 / 12);
		letter-spacing: 0.058em;
		color: #424242;
	}

	&__bottom {
		margin-top: 15px;
	}

	&__item {
		display: flex;
		align-items: center;
	}

	&__item+&__item {
		margin-top: 5px;
	}

	&__value {
		width: 30px;
		height: 30px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__label {
		margin-left: 10px;
		color: #727279;
		font-size: 12px;
		text-transform: lowercase;
	}

	.swiper {
		@media (max-width: 575px) {
			padding-bottom: 70px;
		}
	}

	.swiper-button-prev,
	.swiper-button-next {
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		width: 50px;
		height: 50px;

		@media (max-width: 575px) {
			top: initial;
			bottom: 0;
		}

		&::after {
			content: '';
		}
	}

	.swiper-button-prev {
		background-image: url('../img/svg/arrow-left-slide-2.svg');
	}

	.swiper-button-next {
		background-image: url('../img/svg/arrow-right-slide-2.svg');
	}
}