.faq {
	position: relative;

	&__caption {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		@media (max-width: 575px) {
			flex-direction: column;
			align-items: flex-start;
			gap: 20px;
		}
	}

	&__link {
		font-size: 18px;
		font-weight: 700;
		text-decoration: underline;
		color: #0967be;

		&:hover {
			text-decoration: none;
			color: #0967be;
		}
	}

	&__content {
		margin-top: 75px;

		@media (max-width: 575px) {
			margin-top: 40px;
		}
	}

	&__item {
		&--bg {
			background-color: #fff;
			box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.11);
			border-radius: 16px;
		}
	}

	&__item+&__item {
		margin-top: 40px;

		@media (max-width: 575px) {
			margin-top: 20px;
		}
	}

	&__title {
		font-size: 21px;
		font-weight: 700;
		color: #4c4c4c;
		cursor: pointer;
		background-image: url('../img/svg/plus.svg');
		background-size: 23px;
		background-position: left;
		background-repeat: no-repeat;
		padding-left: 40px;
		transition: all 0.3s ease;

		@media (max-width: 575px) {
			font-size: 18px;
		}

		&.active {
			background-image: url('../img/svg/minus.svg');
			color: $color-primary;

			&:hover {
				color: $color-primary;
			}
		}

		&:hover {
			color: $color-primary;
		}

		&--inner {
			padding: 30px 70px;
			background-position: 30px center;
		}
	}

	&__body {
		display: none;
		color: #4c4c4c;
		line-height: calc(24 / 16);
		padding-top: 10px;

		&--inner {
			padding: 0 30px 30px;
		}
	}
}