.circle {
	position: absolute;
	z-index: -1;

	img {
		opacity: 0.4;
	}

	&--1 {
		right: 190px;
		top: 40px;
	}

	&--2 {
		left: 100px;
		top: 440px;
	}

	&--3 {
		top: 230px;
		right: 122px;
	}

	&--4 {
		top: 40px;
		left: 55px;
	}

	&--5 {
		top: 980px;
		left: 0;
	}

	&--6 {
		bottom: 20px;
		right: 200px;
	}

	&--7 {
		top: 240px;
		right: 440px;
	}

	&--8 {
		bottom: 20px;
		left: 900px;
	}

	&--9 {
		top: 485px;
		left: 85px;
	}

	&--10 {
		top: 1000px;
		right: 135px;
	}

	&--11 {
		bottom: 270px;
		left: 0;
	}

	&--12 {
		bottom: 430px;
		right: 200px;
	}

	&--13 {
		bottom: 50px;
		left: 216px;
	}

	&--14 {
		top: 0;
		left: 700px;
	}

	&--15 {
		bottom: 300px;
		left: 900px;
	}

	&--16 {
		bottom: 150px;
		left: 12px;
	}
}