.checkbox {
	margin-top: 30px;

	label {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		margin-top: 20px;
		font-size: 13px;
		cursor: pointer;

		input {
			display: none;

			&:checked+span {
				background-image: url("../img/svg/checkmark.svg");
			}
		}

		span {
			display: block;
			width: 100%;
			max-width: 18px;
			height: 18px;
			border: 2px solid #727279;
			background-size: 10px;
			background-position: center;
			background-repeat: no-repeat;
			border-radius: 4px;
			filter: invert(50%);
		}

		i {
			font-style: normal;
			font-size: 12px;
			line-height: 53px;
			font-weight: 300;
			letter-spacing: 0.058em;
			color: #727279;
		}
	}
}