.promotion {
	margin-top: 105px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__content {
		position: relative;
		margin-top: 30px;
		display: flex;
		justify-content: space-between;
		padding: 50px 60px;
		background-color: #EFEFEF;
		border-radius: 10px;
		border-top: 15px solid #24458C;

		@media (max-width: 991px) {
			flex-direction: column;
			gap: 60px;
		}

		@media (max-width: 575px) {
			padding: 70px 20px 30px;
		}
	}

	&__left {
		flex: 0 0 24%;
	}

	&__map {
		text-align: center;
	}

	&__price {
		margin-top: 65px;
		font-size: 21px;
		line-height: calc(32 / 21);
		color: #3d3d3d;

		@media (max-width: 991px) {
			margin-top: 20px;
		}

		span {
			font-size: 40px;
			font-weight: 700;

			@media (max-width: 1160px) {
				font-size: 28px;
			}
		}
	}

	&__right {
		flex: 0 0 67%;
	}

	&__text {
		font-size: 21px;
		line-height: calc(32 / 21);
		color: #3d3d3d;

		@media (max-width: 991px) {
			font-size: 18px;
		}

		span {
			font-weight: 700;
		}
	}

	&__btn {
		margin-top: 60px;

		@media (max-width: 991px) {
			margin-top: 30px;
		}
	}

	&__hit {
		position: absolute;
		top: 0;
		right: 60px;
		font-size: 26px;
		font-weight: 700;
		color: #fefeff;
		background-color: #24458C;
		text-transform: uppercase;
		border-radius: 0 0 25px 25px;
		padding: 5px 55px;
	}
}