.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	text-transform: uppercase;
	border-radius: 6px;
	border: 2px solid transparent;
	transition: all 0.3s ease-out;
	font-weight: 700;
	transform: perspective(1px) translateZ(0);
	transition-property: color;
	transition-duration: 0.3s;

	@media (max-width: 575px) {
		font-size: 14px;
		padding: 20px;
		text-align: center;
		width: 100%;
	}

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #fff;
		transform: scaleX(0);
		transform-origin: 0 50%;
		transition-property: transform;
		transition-duration: 0.3s;
		transition-timing-function: ease-out;
	}

	&:hover:before {
		transform: scaleX(1);
	}

	&:hover {
		color: #242424;
	}

	&--red {
		padding: 29px 111px 29px 30px;
		background-color: $color-primary;
		color: #fff;

		@media (max-width: 575px) {
			padding: 20px;
		}

		&:hover {
			color: #242424;

			&:after {
				filter: invert(0);
			}
		}

		&:before {
			background: #fff
		}

		&:after {
			filter: brightness(10);
		}

		&:disabled {
			&:after {
				filter: brightness(10);
			}
		}
	}

	&--white {
		padding: 29px 111px 29px 30px;
		background-color: #fff;

		@media (max-width: 575px) {
			padding: 20px;
		}

		&:before {
			background: $color-primary;
		}
	}

	&--blue {
		padding: 29px 111px 29px 30px;
		background-color: #24458C;
		color: #fff;

		@media (max-width: 575px) {
			padding: 20px;
		}

		&:hover {
			color: #242424;

			&:after {
				filter: invert(0);
			}
		}

		&:before {
			background: #fff;
		}

		&:after {
			filter: brightness(10);
		}
	}

	&--arrow {
		&::after {
			@media (min-width: 576px) {
				content: '';
				background-image: url('../img/svg/arrow-right.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
				position: absolute;
				top: 50%;
				right: 20px;
				transform: translateY(-50%);
				width: 40px;
				height: 20px;
				transition: all 0.3s ease;
			}
		}
	}

	&--animate {
		height: 250px;
		width: 250px;
		position: absolute;
		top: -20px;
		left: -120px;
		background: rgba(255, 255, 255, 1.0);
		border-radius: 100%;
		transform: scale(0);
		animation: ripple 4s linear infinite;
	}

	@keyframes ripple {
		20% {
			opacity: 0;
			transform: scale(2.5);
		}

		100% {
			opacity: 0;
			transform: scale(2.5);
		}
	}

	&:disabled {
		opacity: 0.5;
		cursor: default;
		color: #fff;

		&:before,
		&:hover:before {
			background-color: $color-primary;
		}
	}
}

.btn+.btn {
	margin-left: 15px;

	@media (max-width: 991px) {
		margin: 0;
	}
}