html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

$color-primary: #f2140a;

body {
	min-width: 320px;
	font-family: "SegoeUI", sans-serif;
	font-weight: 400;
	color: #242424;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #242424;
	transition: all 0.3s ease;

	&:hover {
		color: $color-primary;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1160px;

	@media (max-width: 1160px) {
		max-width: 970px;
	}

	@media (max-width: 991px) {
		max-width: 750px;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-weight: 900;
	font-size: 48px;
	line-height: calc(53 / 48);
	text-transform: uppercase;
	letter-spacing: 0.058em;

	&--white {
		color: #fff;
	}

	&--gray {
		color: #737373;

		span {
			display: block;
		}
	}

	span {
		color: $color-primary;
	}

	@media (max-width: 991px) {
		font-size: 36px;
	}

	@media (max-width: 575px) {
		font-size: 24px;
	}
}

.input {
	width: 380px;
	height: 82px;
	box-shadow: inset 0px 0px 6px 0px rgba(0, 0, 0, 0.13);
	padding: 0 20px;
	outline: none;
	color: #8b8b8b;
	border-radius: 6px;

	@media (max-width: 991px) {
		width: 100%;
	}

	@media (max-width: 575px) {
		height: 60px;
	}

	&::placeholder {
		color: #8b8b8b;
	}
}