.contacts {
	position: relative;
	margin: 75px 0;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&::before {
		content: '';
		background-image: url('../img/contacts-bg.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		position: absolute;
		bottom: 0px;
		left: 0px;
		width: 100%;
		height: 100%;
		opacity: 0.3;
	}

	&__title {
		text-align: center;
	}

	&__text {
		max-width: 575px;
		margin: 70px auto 0;
		text-align: center;
		font-size: 21px;
		font-weight: 300;
		color: #5c5c5c;

		@media (max-width: 991px) {
			font-size: 18px;
			margin-top: 30px;
		}
	}

	&__form {
		position: relative;
		z-index: 1;
		margin-top: 40px;
		text-align: center;
	}

	&__group {
		display: flex;
		justify-content: center;
		gap: 20px;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__info {
		margin-top: 70px;

		@media (max-width: 575px) {
			margin-top: 30px;
		}

		.checkbox {
			margin-top: 0;
		}

		.checkbox label {
			justify-content: center;
		}
	}

	&__desc {
		font-size: 21px;
		font-weight: 300;
		color: #5c5c5c;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__link {
		font-weight: 700;
		text-decoration: underline;
		color: #f2140a;

		&:hover {
			text-decoration: none;
		}
	}

	&__tel {
		display: block;
		margin-top: 20px;
		font-size: 48px;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: 0.058em;
		color: #727279;

		@media (max-width: 991px) {
			font-size: 38px;
		}

		@media (max-width: 575px) {
			font-size: 22px;
		}
	}

	&__btn {
		&:disabled {

			&:before,
			&:hover:before {
				background-color: #24458C;
			}

			&:after {
				filter: brightness(10);
			}
		}
	}
}

.radio-contacts {
	margin-top: 55px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	gap: 15px;

	@media (max-width: 575px) {
		margin-top: 25px;
	}

	&__label {
		position: relative;
		padding: 20px 40px;
		background-color: #f0f0f0;
		font-size: 18px;
		color: #727279;
		border-radius: 10px;
		transition: all 0.3s ease;
		cursor: pointer;

		@media (max-width: 991px) {
			flex: 0 0 45%;
		}

		@media (max-width: 575px) {
			padding-left: 20px;
			padding-right: 20px;
			font-size: 14px;
		}

		&:hover {
			background-color: #ddd;
		}

		input {
			display: none;

			&:checked+i {
				background-color: #ddd;
			}
		}

		i {
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 10px;
		}

		span {
			position: relative;
			z-index: 1;
		}
	}
}