.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-color: #fff;
		background-image: url('../img/bg-1.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		max-width: 740px;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70px);
		border-radius: 39px;
		padding: 60px 40px;

		@media (max-width: 767px) {
			margin: 0 15px;
		}

		@media (max-width: 575px) {
			border-radius: 15px;
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	&__title {
		display: flex;
		flex-direction: column;
		color: #6D757F;
		font-size: 32px;
		text-align: center;

		@media (max-width: 575px) {
			font-size: 24px;
		}
	}

	&__text {
		max-width: 600px;
		margin-top: 30px;
		font-size: 18px;
		font-weight: 300;
		text-align: center;
		color: #5c5c5c;

		@media (max-width: 767px) {
			font-size: 16px;
		}
	}

	&__form {
		margin-top: 30px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 767px) {
			flex-direction: column;
			gap: 10px;
		}
	}

	&__input {
		width: 330px;
		height: 50px;
		box-shadow: inset 0px 0px 6px 0px rgba(0, 0, 0, 0.13);
		padding: 0 15px;
		outline: none;
		color: #8b8b8b;
		border-radius: 6px;

		@media (max-width: 767px) {
			width: 100%;
		}
	}

	&__btn {
		background-color: $color-primary;
		color: #fff;
		padding: 15px 50px;
	}

	&__box {
		margin-top: 30px;
		text-align: center;

		p {
			font-size: 18px;
			font-weight: 300;
			color: #5c5c5c;

			@media (max-width: 575px) {
				font-size: 16px;
			}
		}

		label {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			justify-content: center;
			gap: 10px;
			margin-top: 20px;
			font-size: 13px;
			cursor: pointer;

			input {
				display: none;

				&:checked+span {
					background-image: url("../img/svg/checkmark.svg");
				}
			}

			span {
				display: block;
				width: 100%;
				max-width: 18px;
				height: 18px;
				border: 2px solid #727279;
				background-size: 10px;
				background-position: center;
				background-repeat: no-repeat;
				border-radius: 4px;
				filter: invert(50%);
			}

			i {
				font-style: normal;
				font-size: 12px;
				font-weight: 300;
				color: #727279;

				@media (min-width: 380px) {
					letter-spacing: 0.058em;
				}
			}
		}
	}

	&__link {
		color: $color-primary;
		font-weight: 700;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}

	&__tel {
		display: block;
		font-size: 20px;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: 0.058em;
		color: #727279;
		margin: 15px 0;
	}

	&__close {
		position: absolute;
		right: 20px;
		top: 15px;
		font-size: 20px;
		color: #000;
		text-decoration: none;
		z-index: 3;

		img {
			width: 30px;
			height: 30px;
		}
	}
}