.priceswebsite {
	position: relative;
	background-image: url('../img/price-bg-2.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	margin-top: 100px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}


	&__content {
		display: flex;
		justify-content: space-between;
		margin-top: 40px;

		@media (max-width: 991px) {
			flex-wrap: wrap;
			justify-content: center;
			gap: 30px;
		}
	}

	&__item {
		padding: 30px;
		position: relative;
		flex: 0 0 32%;
		border-radius: 10px;
		text-align: center;
		background-color: #EFEFEF;

		@media (max-width: 991px) {
			flex: 0 0 45%;
		}

		@media (max-width: 575px) {
			flex: 0 0 100%;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0px;
			left: 0px;
			width: 100%;
			height: 100%;
		}

		&--1 {
			&::before {
				background-image: url('../img/priceweb-bg-1.png');
			}
		}

		&--2 {
			&::before {
				background-image: url('../img/priceweb-bg-2.png');
			}
		}

		&--3 {
			&::before {
				background-image: url('../img/priceweb-bg-3.png');
			}
		}
	}

	&__top {
		position: relative;
		z-index: 1;
		border-bottom: 2px solid $color-primary;
		padding-bottom: 15px;
	}

	&__subtitle {
		margin-top: 25px;
		font-size: 20px;
		min-height: 46px;
		font-weight: 700;
		color: #3f3f3f;

		@media (max-width: 575px) {
			font-size: 18px;
		}
	}

	&__body {
		margin-top: 20px;
	}

	&__list {
		position: relative;

		li {
			position: relative;
			text-align: left;
			padding-left: 35px;

			@media (max-width: 575px) {
				font-size: 14px;
			}

			&::before {
				content: '';
				background-image: url('../img/svg/check.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 0px;
				left: 0px;
				width: 20px;
				height: 20px;
			}
		}

		li+li {
			margin-top: 10px;
		}
	}

	&__cost {
		font-size: 36px;
		font-weight: 700;
		color: #40414a;
		margin-top: 25px;

		@media (max-width: 575px) {
			font-size: 24px;
		}
	}

	&__bottom {
		position: relative;
		margin-top: 20px;
	}

	&__btn {
		width: 100%;
		padding-top: 12px;
		padding-bottom: 12px;
		text-align: right;

		@media (max-width: 575px) {
			text-align: center;
		}
	}

	&__link {
		display: block;
		margin-top: 10px;
		padding: 7px 0;
		background-color: #fff;
		color: $color-primary;
		border: 1px solid $color-primary;
		text-transform: uppercase;
		line-height: 30px;
		font-weight: 700;
		color: #f2140a;
		border-radius: 6px;
		transform: perspective(1px) translateZ(0);
		transition-property: color;
		transition-duration: 0.3s;

		&:hover {
			&:before {
				transform: scaleX(1);
			}
		}

		&:hover {
			color: #fff;
		}

		&::before {
			content: '';
			position: absolute;
			top: 0px;
			left: 0px;
			width: 100%;
			height: 100%;
			transform: scaleX(0);
			transform-origin: 0 50%;
			transition-property: transform;
			transition-duration: 0.3s;
			transition-timing-function: ease-out;
			background-color: $color-primary;
			z-index: -1;
		}
	}
}