.causes {
	margin-top: 115px;

	@media (max-width: 991px) {
		margin-top: 50px;
	}

	&__content {
		margin-top: 35px;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 30px;

		@media (max-width: 575px) {
			grid-template-columns: repeat(1, 1fr);
			grid-gap: 15px;
		}
	}

	&__item {
		position: relative;
		width: 100%;
		padding: 20px 65px;
		background-color: #ddd;
		border-radius: 10px;

		@media (max-width: 991px) {
			padding-right: 15px;
		}

		@media (max-width: 575px) {
			padding: 15px;
		}

		&:hover {
			&:before {
				@media (min-width: 576px) {
					left: 20px;
				}
			}
		}

		&::before {
			@media (min-width: 576px) {
				content: '';
				background-image: url('../img/svg/arrow-right-2.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 22px;
				left: 10px;
				width: 34px;
				height: 20px;
				transition: all 0.3s ease;
			}
		}
	}

	&__subtitle {
		font-size: 18px;
		line-height: calc(21 / 18);
		font-weight: 700;
		color: #0967be;
	}

	&__text {
		margin-top: 20px;
		line-height: calc(21 / 16);
		font-weight: 400;
		color: #7a7a7a;

		@media (max-width: 575px) {
			margin-top: 10px;
		}
	}
}