.top10 {
	padding-top: 60px;

	@media (max-width: 575px) {
		padding-top: 0;
	}

	&__content {
		margin-top: 40px;
	}

	&__caption {
		display: flex;
		justify-content: space-between;
		gap: 30px;

		@media (max-width: 1160px) {
			flex-direction: column;
		}
	}

	&__text {
		max-width: 730px;
	}

	&__subtitle {
		font-size: 24px;
	}

	&__desc {
		margin-top: 15px;
	}

	&__file {
		display: flex;
		align-items: center;

		&:hover {
			img {
				transform: scale(1.04);
			}
		}

		img {
			width: 60px;
			height: 60px;
			transition: all 0.3s ease;

		}

		p {
			color: #3079FA;
			margin-left: 10px;

			span {
				display: block;
				color: #5D5C5C;
				margin-top: 5px;
			}
		}
	}

	&__items {
		margin-top: 60px;
		display: flex;
		flex-wrap: wrap;
		gap: 15px;
	}

	&__link {
		border: 1px solid $color-primary;
		padding: 10px;
		border-radius: 5px;
		transition: all 0.1s ease;

		@media (max-width: 575px) {
			font-size: 14px;
		}

		&:hover {
			background-color: $color-primary;
			color: #fff;
		}
	}
}