.budget {
	position: relative;
	padding: 70px 0 60px;
	background-image: url('../img/bg-budget.jpg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 23, 59, 0.81);
	}

	&__content {
		position: relative;
		z-index: 1;
		text-align: center;

		@media (min-width: 1161px) {
			max-width: 665px;
			text-align: right;
		}
	}

	&__price {
		display: flex;
		justify-content: flex-end;
		margin-top: 65px;
		font-size: 24px;
		line-height: calc(27 / 24);
		font-weight: 900;
		color: #a2a2a2;

		@media (max-width: 1160px) {
			justify-content: center;
		}

		@media (max-width: 575px) {
			margin-top: 30px;
			flex-direction: column;
		}

		span {
			font-size: 106px;
			font-weight: 900;
			text-transform: uppercase;
			color: #fff;

			@media (min-width: 576px) {
				line-height: 88px;
			}

			@media (max-width: 575px) {
				font-size: 50px;
			}
		}
	}

	&__text {
		margin-top: 40px;
		font-size: 18px;
		line-height: calc(24 / 18);
		font-weight: 300;
		color: #fff;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__btn {
		margin-top: 45px;

		&::before {
			background-color: #0967BF;
		}
	}
}