.switch {
	display: flex;
	margin-top: 30px;

	@media (max-width: 575px) {
		flex-direction: column;
	}

	&__item {
		display: flex;
		align-items: center;
	}

	&__item+&__item {
		@media (min-width: 576px) {
			margin-left: 30px;
		}

		@media (max-width: 575px) {
			margin-top: 20px;
		}
	}

	&__desc {
		margin-left: 20px;
		font-weight: 700;
		color: #7a7a7a;

		@media (min-width: 576px) {
			font-size: 18px;
		}
	}
}

.switch-btn {
	display: inline-block;
	width: 62px;
	height: 24px;
	border-radius: 12px;
	background: #bfbfbf;
	z-index: 0;
	margin: 0;
	padding: 0;
	border: none;
	cursor: pointer;
	position: relative;
	transition-duration: 300ms;

	&:after {
		position: absolute;
		z-index: 1;
		content: "";
		height: 36px;
		width: 36px;
		border-radius: 18px;
		background: #fff;
		top: -6px;
		left: -6px;
		transition-duration: 300ms;
	}
}

.switch-on {
	background: #ddd;

	&:after {
		left: 30px;
		background: $color-primary;
	}
}