.cases {
	position: relative;

	&__content {
		margin-top: 35px;
	}

	&__wrap {
		position: relative;
		z-index: 1;
		background-color: #F0F0F0;
		padding: 25px;
		border-radius: 10px;
	}

	&__wrap+&__wrap {
		margin-top: 70px;
	}

	&__inner {
		display: flex;
		padding: 0 10px;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__left {
		@media (min-width: 992px) {
			width: 50%;
		}
	}

	&__img {
		margin-top: 40px;
		height: 325px;

		@media (min-width: 992px) {
			width: 485px;
		}

		@media (max-width: 575px) {
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__right {
		@media (min-width: 992px) {
			margin-left: 75px;
			width: 50%;
		}

		@media (max-width: 991px) {
			margin-top: 50px;
		}
	}

	&__subtitle {
		margin-top: 15px;
		line-height: calc(24 / 18);
		font-weight: 700;
		color: #222;

		@media (min-width: 576px) {
			font-size: 18px;
		}
	}

	&__boxes {
		margin-top: 15px;
	}

	&__box {
		display: flex;
		gap: 5px;

		@media (max-width: 1160px) {
			flex-direction: column;
		}
	}

	&__box+&__box {
		margin-top: 15px;
	}

	&__label {
		font-weight: 700;
		color: #7a7a7a;

		span {
			font-weight: 400;
			color: #242424;
		}
	}

	&__value {
		font-weight: 400;
	}

	&__link {
		font-size: 18px;
		font-weight: 700;
		color: #7a7a7a;
	}

	&-block {
		margin-top: 30px;

		&__title {
			font-size: 18px;
			font-weight: 700;
			color: #7a7a7a;
			text-transform: uppercase;
		}

		&__items {
			margin-top: 13px;
			border-radius: 10px;
			box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.12);
		}

		&__items--before {
			background-color: #f7f7f7;
		}

		&__item {
			padding: 20px 15px;

			@media (min-width: 576px) {
				display: grid;
				grid-template-columns: 150px 1fr;
				grid-gap: 30px;
			}

		}

		&__label {
			font-weight: 700;
			color: #7a7a7a;

			@media (max-width: 575px) {
				margin-bottom: 10px;
			}
		}

		&__value {
			color: #7a7a7a;
		}

		&__items--after {
			background: rgb(38, 68, 138);
			background: linear-gradient(90deg, rgba(38, 68, 138, 1) 53%, rgba(67, 55, 90, 1) 94%);

			.cases-block__label {
				color: #fff;
			}

			.cases-block__value {
				color: #fff;
			}
		}
	}

	&__holder {
		text-align: center;
		margin-top: 20px;

		.cases__link {
			margin-top: 10px;
			display: block;
		}
	}

	&__slide {
		text-align: center;

		a {
			img {
				transition: all 0.3s ease;

				&:hover {
					transform: scale(1.03);
				}
			}
		}
	}
}