.breadcrumbs {
	position: relative;
	z-index: 1;
	padding: 25px 0;

	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__item {
		font-size: 13px;
		font-weight: 300;
		letter-spacing: 0.058em;
		color: #2f2f2f;
	}

	&__item+&__item {
		padding-left: 10px;

		&::before {
			content: "/";
			display: inline-block;
			padding-right: 10px;
		}
	}

	&__link {
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}
}