.intro {
	position: relative;
	z-index: 1;

	@media (min-width: 576px) {
		background-image: url('../img/bg-intro.jpg');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
	}

	@media (max-width: 575px) {
		background-color: #232524;
	}

	@media (min-width: 992px) {
		min-height: 580px;
	}

	&::before {
		content: '';
		background-image: url('../img/waves.png');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: 0px;
		right: 0px;
		width: 531px;
		height: 187px;
	}

	.breadcrumbs__link,
	.breadcrumbs__item {
		color: #fff;
	}

	&__wrap {
		padding: 70px 0;

		@media (max-width: 767px) {
			padding: 40px 0;
		}
	}

	&__content {
		display: flex;

		@media (min-width: 992px) {
			gap: calc(45px * 2);
		}

		@media (max-width: 991px) {
			flex-direction: column;
			gap: 30px;
		}
	}

	&__left {
		position: relative;
		flex: 0 0 20%;

		&::before {
			@media (min-width: 992px) {
				content: '';
				position: absolute;
				top: 0;
				right: calc(-1 * 45px);
				height: 100%;
				border-right: 5px solid #fff;
			}
		}
	}

	&__title {
		font-size: 64px;
		line-height: calc(75 / 64);
		letter-spacing: normal;

		@media (max-width: 991px) {
			font-size: 48px;
		}

		@media (max-width: 575px) {
			font-size: 28px;
		}
	}

	&__btn {
		padding: 36px 115px 36px 40px;
		margin-top: 65px;

		@media (max-width: 991px) {
			margin-top: 40px;
		}

		@media (max-width: 575px) {
			padding: 20px;
		}
	}

	&__text {
		flex: 0 0 30%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__desc {
		position: relative;
		z-index: 1;
		font-size: 18px;
		color: #fff;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__price {
		font-size: 39px;
		font-weight: 900;
		color: #fff;

		@media (max-width: 991px) {
			margin-top: 15px;
		}

		@media (max-width: 575px) {
			font-size: 22px;
		}
	}
}

.advantage-intro {
	margin-top: 55px;
	display: flex;
	gap: 40px;

	@media (max-width: 767px) {
		flex-direction: column;
	}

	&__item {
		position: relative;
		font-size: 18px;
		font-weight: 300;
		color: #fff;
		padding-left: 45px;

		@media (min-width: 992px) {
			flex: 0 0 23%;
		}

		@media (max-width: 1160px) {
			font-size: 14px;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/check-3.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 0;
			width: 30px;
			height: 30px;
			transform: translateY(-50%);
		}
	}

}