.cms {
	margin-top: 100px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__wrap {
		margin-top: 30px;
		background-image: url('../img/cms-bg.jpg');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		padding: 90px 0;
	}

	.container {
		position: relative;
	}

	&__item {
		width: 175px;
		height: 175px;
		background-color: #fff;
		border-radius: 6px;
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			border-radius: 6px;
			transition: all 0.3s ease;

			&:hover {
				transform: scale(1.05);
			}
		}
	}

	.swiper-slide {
		display: flex;
		justify-content: center;
	}

	.swiper-button-prev,
	.swiper-button-next {
		background-size: 35px;
		background-position: center;
		background-repeat: no-repeat;
		width: 46px;
		height: 46px;
		background-color: #fff;
		border-radius: 50%;

		&::after {
			content: '';
		}
	}

	.swiper-button-prev {
		background-image: url('../img/svg/arrow-left-slide-4.svg');
		left: -40px;

		@media (max-width: 1250px) {
			left: 0;
		}
	}

	.swiper-button-next {
		background-image: url('../img/svg/arrow-right-slide-4.svg');
		right: -40px;

		@media (max-width: 1250px) {
			right: 0;
		}
	}
}