.price {
	padding-top: 80px;

	@media (max-width: 575px) {
		padding-top: 50px;
	}

	&__title {
		max-width: 700px;
	}

	&__content {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: 30px;
		margin-top: 60px;
		border-radius: 10px;

		@media (min-width: 576px) {
			background-color: #fff;
			padding: 20px;
		}

		@media (max-width: 1160px) {
			justify-content: center;
		}

		@media (max-width: 575px) {
			margin-top: 30px;
		}
	}

	&__item {
		@media (min-width: 576px) {
			flex: 0 0 325px;
		}
	}

	&__caption {
		background: linear-gradient(90deg, #26448a 53%, #43375a 94%);
		color: #fff;
		padding: 15px 20px;
		border-radius: 10px 10px 0 0;
		text-align: center;

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
		}

		p {
			line-height: 29px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 0.058em;
			color: #fff;

			@media (max-width: 575px) {
				font-size: 14px;
			}
		}

		span {
			font-size: 24px;
			font-weight: 400;

			@media (max-width: 575px) {
				font-size: 18px;
			}
		}
	}

	&__list {
		background-color: #F0F0F0;
		padding: 25px 20px 80px;
		list-style-position: inside;

		li+li {
			margin-top: 15px;
		}

		li {
			position: relative;
			color: #6f6f6f;
			padding-left: 25px;

			@media (min-width: 576px) {
				letter-spacing: 0.058em;
			}

			&::before {
				content: '';
				background-image: url('../img/svg/check.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 5px;
				left: 0px;
				width: 16px;
				height: 16px;
			}
		}
	}

	&__btn {
		background-color: #24458C;
		width: 100%;
		text-align: center;
		color: #fff;
		border-radius: 0 0 10px 10px;
		padding: 22px 0;
		letter-spacing: 0.058em;
		font-size: 21px;
		border-width: 1px;

		@media (max-width: 575px) {
			font-size: 18px;
		}

		&:hover {
			color: #fff;
			border-color: $color-primary;
		}

		&:before {
			background-color: $color-primary;
		}
	}
}