.header {

	&__top {
		background-color: #fff;
		padding: 15px 0;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		@media (max-width: 991px) {
			align-items: center;
		}
	}

	&__logo {
		position: relative;
		z-index: 10;
		width: 156px;
		height: 94px;

		@media (max-width: 991px) {
			width: 127px;
			height: 76px;
		}


		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
	}

	&__actions {
		@include aic;

		@media (max-width: 991px) {
			justify-content: center;
			flex-direction: column;
		}
	}

	&__info {
		@include aic;
		margin-top: 15px;

		@media (max-width: 575px) {
			margin-top: 0;
		}
	}

	&__buttons {
		margin-left: 40px;

		@media (max-width: 1160px) {
			margin-left: 20px;
		}

		@media (max-width: 991px) {
			order: -1;
		}

		@media (max-width: 991px) {
			position: relative;
			z-index: 10;
			margin: 0;
		}
	}

	&__btn {
		background-color: $color-primary;
		color: #fff;
		padding: 14px 24px;

		&--service {
			@media (max-width: 991px) {
				display: none;
			}
		}

		&--call {
			i {
				font-style: normal;

				@media (max-width: 991px) {
					display: none;
				}
			}

			img {
				display: none;

				@media (max-width: 991px) {
					display: block;
				}
			}
		}

		@media (max-width: 991px) {
			padding: 10px 15px;
			font-size: 14px;
		}

		@media (max-width: 991px) {
			padding: 0;
			margin-left: 0;
			width: 50px;
			height: 50px;
			background-color: transparent;
		}
	}
}

.actions-header {
	@media (max-width: 991px) {
		order: 1;
	}

	&__location {
		position: relative;
		line-height: calc(24 / 16);
		text-decoration: underline;
		color: #383838;
		padding-left: 20px;
		margin-right: 45px;

		@media (max-width: 767px) {
			margin-right: 20px;
		}

		@media (max-width: 991px) {
			margin: 30px 0 0;
		}

		&:hover {
			text-decoration: none;
			color: inherit;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/location.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 12px;
			height: 17px;
		}
	}

	&__tel {
		font-size: 20px;
		font-weight: 700;
		letter-spacing: 0.058em;
		color: #383838;

		@media (max-width: 1160px) {
			font-size: 16px;
		}
	}
}

.body-header {
	background-color: #ddd;
	padding: 15px 0;

	@media (max-width: 991px) {
		background-color: #fff;
		padding: 0;
	}

	&__menu {
		margin: 0 auto;
		padding: 0 15px;
		max-width: 1160px;
	}

	&__list {
		display: flex;
		position: relative;

		@media (max-width: 1160px) {
			justify-content: center;
		}

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-top: 1px solid #F2F2F2;
				position: relative;

				&:first-child {
					border-top: none;
				}

				&:last-child {
					border-bottom: 1px solid #F2F2F2;
				}
			}

			&:not(:last-child) {
				margin-right: 75px;

				@media (max-width: 1160px) {
					margin-right: 30px;
				}

				@media (max-width: 991px) {
					margin-right: 0;
				}
			}

			@media (min-width: 992px) {
				&:hover .body-header__sub {
					opacity: 1;
					visibility: visible;
					top: 100%;
					pointer-events: all;
				}
			}

			&>a {
				font-size: 18px;
				letter-spacing: 0.058em;

				@media (max-width: 991px) {
					display: block;
					font-size: 14px;
					font-weight: 700;
					text-transform: uppercase;
					letter-spacing: .025em;
					color: #3a3a3a;
					padding: 20px 0;
				}
			}
		}
	}

	&__sub {
		@media (min-width: 992px) {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			background-color: #ddd;
			transition: all 0.3s ease;
			padding: 30px 25px;
			z-index: 2;
			border-radius: 0 0 6px 6px;
			top: 45px;
			pointer-events: none;
		}

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__sublink {
		display: block;
		font-size: 18px;
		color: #242424;

		@media (max-width: 991px) {
			font-size: 14px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: .025em;
			color: #3a3a3a;
			padding: 20px 0;
			border-top: 1px solid #F2F2F2;

			&:nth-child(3) {
				border-top: none;
			}
		}
	}

	&__sublink+&__sublink {
		margin-top: 15px;

		@media (max-width: 991px) {
			margin-top: 0;
		}
	}

	&__sublist {
		list-style: disc;
		list-style-position: inside;
		margin: 20px 0 25px;

		@media (max-width: 991px) {
			list-style: none;
			margin: 0;
		}

		a {
			@media (max-width: 991px) {
				display: block;
				font-size: 14px;
				font-weight: 700;
				text-transform: uppercase;
				letter-spacing: .025em;
				color: #3a3a3a;
				padding: 20px 0;
			}
		}

		li {
			@media (max-width: 991px) {
				border-top: 1px solid #F2F2F2;

				&:last-child {
					border-bottom: 1px solid #F2F2F2;
				}
			}
		}

		li+li {
			margin-top: 15px;

			@media (max-width: 991px) {
				margin-top: 0;
			}
		}
	}
}

.menu {
	&__body {
		@media (max-width: 991px) {
			background-color: #fff;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 130px 20px 20px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 106px;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}


	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-top: 1px solid #F2F2F2;

				&:last-child {
					border-bottom: 1px solid #F2F2F2;
				}
			}

			&>a {
				position: relative;
				font-weight: 700;
				text-transform: uppercase;
				letter-spacing: 0.025em;
				color: #3a3a3a;

				@media (min-width: 992px) {
					padding-bottom: 5px;
					transition: color .1s ease-in;

					&::before {
						content: "";
						position: absolute;
						left: 51%;
						right: 51%;
						bottom: 0;
						background: $color-primary;
						height: 2px;
						transition-property: left, right;
						transition-duration: .3s;
						transition-timing-function: ease-out;
					}

					&:hover:before {
						left: 0;
						right: 0;
					}

					&:hover {
						color: $color-primary;
					}
				}

				@media (max-width: 1160px) {
					font-size: 14px;
				}

				@media (max-width: 991px) {
					padding: 20px 0;
					display: block;
				}
			}
		}

		&>li+li {
			margin-left: 30px;

			@media (max-width: 991px) {
				margin-left: 0;
			}
		}
	}

	&__info {
		&--mob {
			display: none;

			@media (max-width: 991px) {
				display: block;
			}

			.socials__link {
				width: 33px;
				height: 33px;
			}

			.socials__link+.socials__link {
				margin-left: 35px;
			}
		}
	}
}

@media (max-width: 991px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35px;
		height: 20px;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: $color-primary;
		border-radius: 10px;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0px, -50%);
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}

	.menu__icon i {
		content: "";
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1;
		width: 50px;
		height: 50px;
		background: transparent;
		border-radius: 7px;
		opacity: 0.5;
		transition: 0.3s ease-out;
	}

	.menu__arrow {
		&:before {
			content: '';
			background-image: url("../img/svg/arrow-down.svg");
			background-size: 25px;
			background-position: right;
			background-repeat: no-repeat;
			position: absolute;
			top: 0;
			right: 0;
			cursor: pointer;
			width: 100%;
			height: 55px;
			transition: all 0.3s ease;
		}
	}
}