.stages {
	position: relative;
	margin-top: 108px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__wrap {
		position: relative;
		margin-top: 20px;
		background-color: #F1F1F1;
	}

	&__content {
		padding: 40px 0 100px;
	}

	&__subtitle {
		color: #000;
		font-weight: 700;
		font-size: 28px;

		@media (max-width: 575px) {
			font-size: 20px;
		}

		span {
			color: $color-primary;
			font-size: 80px;
			font-weight: 400;

			@media (max-width: 575px) {
				font-size: 50px;
			}
		}
	}

	&__list {
		margin-top: 40px;

		@media (max-width: 575px) {
			margin-top: 25px;
		}

		li {
			position: relative;
			padding-left: 40px;

			@media (max-width: 575px) {
				font-size: 14px;
				padding-left: 25px;
			}

			&::before {
				content: '';
				background-image: url('../img/svg/check-stages.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 0px;
				left: 0px;
				width: 20px;
				height: 20px;

				@media (max-width: 575px) {
					width: 15px;
					height: 15px;
				}
			}
		}

		li+li {
			margin-top: 20px;
		}
	}

	&__btn {
		margin-top: 60px;
	}

	&__box {
		margin-top: 40px;
		text-align: center;
	}

	&__link {
		display: inline-block;
		color: $color-primary;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}
}

.tabs-stages {
	display: flex;
	gap: 20px;
	margin-top: 0;

	@media (max-width: 991px) {
		flex-direction: column;
	}

	&__list {
		display: flex;
		flex: 0 0 30%;
		flex-direction: column;
		background-color: #fff;
		counter-reset: num;
	}

	&__item {
		background-color: #fff;
		width: 100%;
		text-align: left;
		border-radius: 0;
		line-height: calc(20 / 16);
		font-weight: 400;
		color: #000;
		padding-left: 45px;
		transition: all 0.1s ease;

		@media (max-width: 1160px) {
			padding-right: 10px;
			padding-left: 40px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}

		&:first-child {
			@media (max-width: 991px) {
				border-radius: 0;
			}
		}

		&::before {
			position: absolute;
			left: 20px;
			content: counter(num) '.';
			counter-increment: num;
			padding-right: 20px;

			@media (max-width: 1160px) {
				left: 15px;
			}
		}

		&.active {
			background-color: $color-primary;
			color: #fff;
		}

		&:hover {
			background-color: $color-primary;
			color: #fff;
		}
	}

	&__content {
		flex: 0 0 70%;
		background-color: #fff;
		padding: 20px 40px;
		border-radius: 0;

		@media (max-width: 575px) {
			padding-left: 20px;
			padding-right: 20px;
		}
	}
}