.work {
	position: relative;
	margin-top: 90px;
	background-image: url('../img/bg-work.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	margin-bottom: 25px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__subtitle {
		font-size: 40px;
		font-weight: 900;
		color: #727279;
		max-width: 540px;

		@media (max-width: 575px) {
			font-size: 24px;
		}
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 30px 70px;
		margin-top: 30px;

		@media (max-width: 767px) {
			grid-gap: 30px;
		}

		@media (max-width: 575px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__item {
		background-color: #f7f7f7;
		box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.38);
		padding: 25px;
		border-radius: 10px;
		transition: all 0.3s ease;
		line-height: calc(24 / 16);
		color: #7a7a7a;

		@media (min-width: 992px) {
			border-left: 15px solid transparent;
		}

		@media (max-width: 767px) {
			padding: 20px 15px;
		}

		&:hover {
			@media (min-width: 992px) {
				border-color: #274386;
			}
		}
	}

	&__body {
		margin-top: 60px;
	}

	&__box {
		margin-top: 25px;
		display: flex;
		gap: 20px;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__text {
		flex: 0 0 45%;
		color: #6f6f6f;
		line-height: calc(24 / 16);

		p+p {
			margin-top: 30px;
		}
	}
}