.video {
	&__player {
		position: relative;
		background-color: transparent;
		display: flex;
		border-radius: 60px;
		flex: 0 0 55%;
		height: 420px;
		background-color: #E2E2E2;
		border-radius: 60px;

		iframe,
		img {
			width: 100%;
			height: 100%;
			display: block;
			object-fit: cover;
			border: none;
			border-radius: 60px;

			@media (max-width: 991px) {
				height: 500px;
				border-radius: 0;
			}

			@media (max-width: 575px) {
				height: auto;
			}
		}
	}

	&__btn {
		width: 154px;
		height: 154px;
		border: none;
		background-color: transparent;
		background-image: url("../img/play-btn.png");
		background-size: cover;
		background-repeat: no-repeat;
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 10;
		cursor: pointer;

		@media (max-width: 575px) {
			width: 100px;
			height: 100px;
		}
	}
}