.seo {
	position: relative;
	margin-top: 121px;

	@media (max-width: 991px) {
		margin-top: 50px;
	}

	&__top {
		@include jcsb;
	}

	&__cases {
		font-size: 18px;
		font-weight: 700;
		text-decoration: underline;
		color: #f2140a;

		@media (max-width: 991px) {
			font-size: 16px;
		}

		@media (max-width: 575px) {
			display: none;
		}

		&:hover {
			text-decoration: none;
		}
	}
}