.socials {
	@include aic;
	margin-right: 30px;

	@media (max-width: 991px) {
		margin: 20px 0;
	}

	&__link {
		width: 20px;
		height: 20px;

		&:not(:last-child) {
			margin-right: 15px;
		}

		&:hover img {
			filter: invert(0);
		}


		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			filter: invert(50%);
			transition: all 0.3s ease;
		}
	}
}