.portfolio {
	margin-top: 100px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__swiper {
		margin-top: 25px;
	}

	&__link {
		display: block;

		@media (min-width: 320px) {
			height: 350px;
		}

		@media (min-width: 1160px) {
			height: 300px;
		}

		@media (min-width: 1400px) {
			height: 480px;
		}


		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: all 0.3s ease;

			@media (max-width: 575px) {
				object-fit: contain;
			}

			&:hover {
				opacity: 0.9;
			}
		}
	}

	.swiper-wrapper {
		@media (max-width: 575px) {
			padding-bottom: 55px;
		}
	}

	.swiper-button-prev,
	.swiper-button-next {
		background-size: 35px;
		background-position: center;
		background-repeat: no-repeat;
		width: 50px;
		height: 50px;
		background-color: #0967BE;

		&::after {
			content: '';
		}
	}

	.swiper-button-prev {
		background-image: url('../img/svg/arrow-left-slide-3.svg');
		left: 0;
	}

	.swiper-button-next {
		background-image: url('../img/svg/arrow-right-slide-3.svg');
		right: 0;
	}

	.swiper-pagination-bullet {
		width: 15px;
		height: 15px;
		opacity: 1;
		background: rgba(9, 102, 190, 0.4);
	}

	.swiper-pagination-bullet-active {
		background: #0967BE;
	}
}