.footer {
	background-color: #24458C;
	padding: 20px 0 30px;

	&__top {
		display: grid;
		grid-template-columns: repeat(5, auto);
		grid-gap: 20px;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 30px;
		}

		@media (max-width: 430px) {
			grid-template-columns: 1fr;
		}
	}

	&__logo {
		@media (max-width: 991px) {
			grid-column: 1/3;
			text-align: center;
		}

		@media (max-width: 430px) {
			grid-column: 1;
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: center;

		@media (max-width: 991px) {
			align-items: center;
			text-align: center;

			&:last-child {
				order: -1;
			}
		}

		a {
			font-weight: 700;
			color: #fff;

			&:hover {
				color: $color-primary;
			}
		}

		p {
			max-width: 155px;
			font-size: 13px;
			line-height: calc(16 / 13);
			color: #aeaeae;
			margin-top: 5px;
		}

		span {
			font-weight: 700;
			color: #fff;
		}
	}

	&__btn {
		padding: 12px 88px 12px 12px;

		@media (max-width: 575px) {
			text-align: center;
			padding: 10px 20px;
		}
	}

	&__info {
		@media (max-width: 991px) {
			text-align: center;
		}

		p {
			margin-top: 10px;
			font-size: 13px;
			line-height: calc(16 / 13);
			font-weight: 400;
			color: #fff;
		}

		.socials {
			margin-top: 10px;

			@media (max-width: 991px) {
				justify-content: center;
			}
		}
	}

	&__body {
		margin-top: 15px;
		padding: 30px 0;
		border-top: 1px solid #375CA0;
		border-bottom: 1px solid #375CA0;
		display: grid;
		grid-template-columns: repeat(5, auto);
		align-items: flex-start;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, auto);
			grid-gap: 30px;
		}

		@media (max-width: 400px) {
			grid-template-columns: 1fr;
		}
	}

	&__subtitle {
		text-transform: uppercase;
		font-weight: 700;
		text-decoration: underline;
		color: #fff;

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

	&__subtitle+&__subtitle {
		margin-top: 10px;
	}

	&__list {
		margin-top: 10px;

		li {
			a {
				font-size: 13px;
				line-height: calc(16 / 13);
				color: #a5a5a5;
				transition: all 0.3s ease;

				&:hover {
					color: #fff;
				}
			}
		}

		li+li {
			margin-top: 5px;
		}
	}

	&__items {
		.footer__item+.footer__item {
			margin-top: 20px;
		}
	}

	&__bottom {
		@include jcsb;
		margin-top: 30px;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
			gap: 15px;
		}
	}

	&__desc {
		font-size: 13px;
		line-height: calc(20 / 13);
		color: #959595;
	}

	&__link {
		font-size: 13px;
		color: #959595;
		text-decoration: underline;

		&:hover {
			color: #959595;
			text-decoration: none;
		}
	}
}