.up {
	display: none;
	width: 40px;
	height: 40px;
	position: fixed;
	bottom: 30px;
	right: 30px;
	cursor: pointer;
	z-index: 1;
	opacity: 0.5;
	transition: all 0.3s ease;
	background-color: $color-primary;
	border-radius: 10px;

	@media (max-width: 575px) {
		bottom: 10px;
		right: 10px;
	}

	&:hover {
		opacity: 1;
	}

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;

		img {
			filter: brightness(10);
			display: flex;
			align-items: center;
			justify-content: center;
			width: 27px;
			height: 14px;
			transform: rotate(-90deg);
		}
	}
}