.partners {
	position: relative;
	margin-top: 100px;

	&__wrap {
		position: relative;
	}

	&__swiper {
		margin-top: 85px;
	}

	&__link {
		display: block;
		width: 100%;
		height: 60px;
		padding: 0 50px;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			transition: all 0.3s ease;

			&:hover {
				transform: scale(1.05);
			}
		}
	}

	.swiper-slide {
		display: flex;
		justify-content: center;
	}

	.swiper-button-prev,
	.swiper-button-next {
		background-size: 35px;
		background-position: center;
		background-repeat: no-repeat;
		width: 46px;
		height: 46px;
		background-color: #0967BE;
		border-radius: 50%;

		&::after {
			content: '';
		}
	}

	.swiper-button-prev {
		background-image: url('../img/svg/arrow-left-slide-3.svg');
		left: -40px;

		@media (max-width: 1250px) {
			left: 0;
		}
	}

	.swiper-button-next {
		background-image: url('../img/svg/arrow-right-slide-3.svg');
		right: -40px;

		@media (max-width: 1250px) {
			right: 0;
		}
	}
}