.advantage {
	position: relative;
	margin-top: 110px;

	@media (max-width: 575px) {
		margin-top: 50px;
	}

	&__text {
		font-size: 18px;
		line-height: calc(26 / 18);
		font-weight: 300;
		color: #fff;

		@media (min-width: 1161px) {
			max-width: 400px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__btn {
		margin-top: 30px;
		padding: 29px 111px 29px 30px;
		color: #0d65c0;
		background-color: #fff;

		@media (min-width: 1161px) {
			max-width: 350px;
		}

		@media (max-width: 575px) {
			padding: 20px;
		}

		&:hover {
			color: #0d65c0;
		}

		&.btn--arrow {
			&:hover:after {
				right: 10px;
			}

			&:hover:before {
				background-color: #fff;
			}

			&::after {
				background-image: url('../img/svg/arrow-right-3.svg');
				background-size: cover;
			}
		}
	}

	&__img {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 310px;
		height: 360px;

		@media (max-width: 1160px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&-tabs {
		display: flex;
		justify-content: space-between;
		gap: 25px;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&__list {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			flex: 0 0 35%;
		}

		&__item {
			position: relative;
			width: 100%;
			display: flex;
			align-items: center;
			min-height: 64px;
			text-align: left;
			border-radius: 10px;
			font-size: 16px;
			padding: 14px 20px;
			transition: all 0.1s ease;
			font-weight: 400;

			@media (max-width: 575px) {
				padding: 14px 75px 14px 20px;
				font-size: 14px;
			}

			&:first-child {
				@media (max-width: 991px) {
					border-radius: 10px;
				}
			}

			span {
				display: block;
				max-width: 250px;
			}

			&--1 {
				&::before {
					background-image: url('../img/advantage-icon-1.png');
					background-size: 45px;
				}
			}

			&--2 {
				&::before {
					background-image: url('../img/advantage-icon-2.png');
					background-size: 35px;
				}
			}

			&--3 {
				&::before {
					background-image: url('../img/advantage-icon-3.png');
					background-size: 35px;
				}
			}

			&--4 {
				&::before {
					background-image: url('../img/advantage-icon-4.png');
					background-size: 30px;
				}
			}

			&::before {
				content: '';
				background-repeat: no-repeat;
				background-position: center;
				position: absolute;
				top: 50%;
				right: 20px;
				width: 53px;
				height: 38px;
				transform: translateY(-50%);
				filter: invert(50%);
				transition: all 0.3s ease;

			}

			&:hover {
				&:before {
					filter: brightness(10);
				}
			}

			&:hover,
			&.active {
				background-color: $color-primary;
				color: #fff;
			}

			&.active {
				&:before {
					filter: brightness(10);
				}
			}
		}

		&__item+&__item {
			margin-top: 5px;
		}

		&__content {
			position: relative;
			flex: 0 0 62%;
			flex-direction: column;
			justify-content: space-between;
			border-radius: 10px;
			background: rgb(10, 103, 191);
			background: linear-gradient(90deg, rgba(10, 103, 191, 1) 53%, rgba(141, 35, 242, 1) 89%);

			&.active {
				display: flex;
			}
		}
	}
}